import React, { useState, useRef } from "react";
import Modal from './Modal';
import './Favorites.css';
import { AiOutlineDelete } from 'react-icons/ai';

const Favorites = () => {

    const [showModal, setShowModal] = useState(false)
    const [selectedImage, setSelectedImage] = useState(null)
    const [selectedTitle, setSelectedTitle] = useState(null)
    const inputRef = useRef();

    const getFavoritesFromLocalStorage = () => {
        let favorites = JSON.parse(localStorage.getItem('favorites'));
        if (favorites) {
            favorites = JSON.parse(localStorage.getItem('favorites'));
        }
        else {
            favorites = []
        }
        return favorites
    }

    const [favorites, setFavorites] = useState(getFavoritesFromLocalStorage());

    // useState(() => {
    //     getFavoritesFromLocalStorage()
    // })

    const selectImage = (name, source) => {
        let image = source;
        let title = name;
        if (image) {
            setShowModal(true)
            setSelectedImage(image)
            selectTitle(title)
        }
    }

    const selectTitle = (title) => {
        if (title) {
            setSelectedTitle(title)
        }
    }

    const closeModal = () => {
        setShowModal(false)
    }

    const removeFromFavorites = (url) => {
        const updatedFavorites = favorites.filter((image) => image !== url);
        setFavorites(updatedFavorites)
        localStorage.setItem("favorites", JSON.stringify(updatedFavorites))
    }

    return (
        <section className="favorites">
            <div className="favorites-content">
                <div className="favorites-container">
                    {favorites.map((item) => {
                        const name = item.title;
                        const source = item.url;
                        return <div key={inputRef} className="favorite-item" >
                            <img src={item.url} alt={item.title} className="favorites-img img" onClick={() => selectImage(name, source, true)} />
                            <button className='remove-btn' onClick={() => removeFromFavorites(item)}><AiOutlineDelete /></button>
                        </div>
                    })}
                </div>
                {showModal && <Modal source={selectedImage} title={selectedTitle} closeModal={closeModal} />}
            </div>
        </section>
    )
}

export default Favorites