import React, { Component, useEffect } from "react";
import PhotoContextProvider from "./context/PhotoContext";
import { HashRouter, Route, Switch, Redirect } from "react-router-dom";
import Header from "./components/Header";
import Item from "./components/Item";
import Search from "./components/Search";
import Favorites from './components/Favorites';
import Headertitle from "./components/Headertitle";

class App extends Component {

  handleSubmit = (e, history, searchInput) => {
    e.preventDefault();
    e.currentTarget.reset();
    let url = `/search/${searchInput}`;
    history.push(url);
  };

  render() {
    return (<>
      <PhotoContextProvider>
        <HashRouter className="hash-router" basename="/">
          <div className="container">
            <Headertitle />
            <Route exact path="/" render={() => <div><Favorites /><Redirect to="/" /></div>} />
            <Route render={props => (<div><Header handleSubmit={this.handleSubmit} history={props.history} /></div>)} />
            <Switch>
              <Route path="/strand" render={() => <Item searchTerm="strand" />} />
              <Route path="/lente" render={() => <Item searchTerm="lente" />} />
              <Route path="/zomer" render={() => <Item searchTerm="zomer" />} />
              <Route path="/herfst" render={() => <Item searchTerm="herfst" />} />
              <Route path="/winter" render={() => <Item searchTerm="winter" />} />
              <Route path="/search/:searchInput" render={props => (
                <Search searchTerm={props.match.params.searchInput} />
              )}
              />
            </Switch>
          </div>
        </HashRouter>
      </PhotoContextProvider >
    </>
    );
  }
}

export default App;
