import React, { useState, useRef, useEffect } from "react";
import { AiFillLike, AiFillLinkedin } from 'react-icons/ai';
import Modal from './Modal';
import './Image.css';
import Favorites from './Favorites';

const Image = ({ url, title }) => {

  const [showModal, setShowModal] = useState(false)
  const [selectedImage, setSelectedImage] = useState(null)
  const inputRef = useRef();

  const selectImage = (url) => {
    let image = url;
    if (image) {
      image = favorites.find((image) => image.url === url);
    }
    setSelectedImage(image);
    setShowModal(true)
  }

  const closeModal = () => {
    setShowModal(false)
  }

  const getFavoritesFromLocalStorage = () => {
    let favorites = JSON.parse(localStorage.getItem('favorites'));
    if (favorites) {
      favorites = JSON.parse(localStorage.getItem('favorites'));
    }
    else {
      favorites = []
    }
    return favorites
  }

  const [favorites, setFavorites] = useState(getFavoritesFromLocalStorage());

  const addToFavorites = (url, title) => {
    const image = { title: title, url: url };
    //const findFavorite = favorites.find((image) => image.title !== title);
    const alreadyFavorite = favorites.map(image => image.title).includes(image.title);
    // const alreadyFavorite = image;
    if (alreadyFavorite)
      return
    const updatedFavorites = [...favorites, image]
    setFavorites(updatedFavorites)
    // alert(1)
    console.log(updatedFavorites)
    localStorage.setItem("favorites", JSON.stringify(updatedFavorites))
    getFavorites()
  }

  function getFavorites() {
    // window.location.reload()
    window.location.assign(window.location.href = "/foto-favorieten-app/")
    // window.location.assign(window.location.href)
  }

  return (
    <>
      <li>
        <img className="image" src={url} alt={title} favorites={favorites} style={{ marginBottom: "20px" }} id={inputRef} selectedImage={selectedImage} onClick={() => selectImage(url)} />
        <button id="button" className='like-btn' onClick={() => addToFavorites(url, title)}><AiFillLike /></button>
      </li>

      <div>{showModal && <Modal source={url} title={title} closeModal={closeModal} />}</div>
    </>
  )
};

export default Image;
